.article{
    &__text{
        padding-bottom: 46px;
    }
    .gallery__video-text {
        position: absolute;
        top: 327px;
        left: 552px;
    }
    &__title{
        @include titleEco;
        color:black;
        @include flex;
        justify-content: center;
        padding-top: 62px;
    }
    &__subtitle{
        font-family: 'Raleway';
font-style: normal;
font-weight: 500;
font-size: 18px;
line-height: 22px;
margin-left: -2px;
text-align: center;
letter-spacing: 0em;
color: #1A2F3F;
margin-top: 22px;
    }
    .news{
        margin-top: -7px;
        margin-left: 2px;
        &__items{
            margin-left: 0;
        }
      
        .data{
            margin-bottom: 7px;
            padding-left: 0;
        }
        .data1{
           margin-left: -1px;
            margin-bottom: 9px;

           }
           .data2{
            margin-left: -3px;
             margin-bottom: 9px;
            }
            .data3{
                margin-left: -5px;
               margin-bottom: 9px;
            }
        &__item-title{
            padding-left: 0px;

        }
        &__item-img{
            margin-bottom: 18px;
        }
        &__item-title3{
            margin-left: -4px;
        }
        &__item-subtitle{
            padding-left: 2px;
            margin-bottom: 20px;
            margin-top: 15px;
            letter-spacing: -0.002em;
           
        }
        &__item-subtitle2{
            margin-top: 16px;
            letter-spacing: -0.002em;
            margin-left: -1px;
            margin-bottom: 20px;
            line-height: 20.6px;
            padding-left: 0;
          
        }
        &__item-subtitle3{
            margin-left: -4px;
            margin-bottom: 20px;
            line-height: 20px;
        }
        &__item-group{
            padding-left: 0;
            margin-left: 1px;
            button{
                font-family: 'Raleway';
font-style: normal;
font-weight: 700;
font-size: 14px;
line-height: 20px;
letter-spacing: 0.01em;
color: #1A2F3F;

            }
            img{
                left: 51px;
                top: -2px;
            }
        }
        
        &__item-btn{
            padding-left: 0;
        }
        .mySwiper{
            width: 1287px;
        }
        .news__item:nth-child(2){
            .news__item-group{
                padding-left: 0;
            margin-left: -1px;
            }
        }
        .news__item:nth-child(3){
            .news__item-group{
                padding-left: 0;
            margin-left: -4px;
            }
            img{
                
                margin-left: -2px;
            }
        }
        .news__button{
            display: none;
        }
      
       
    }
    .news__text{
        display: none;
    }
    .arrows{
        display: none;
    }
    .gallery__video{
        margin-top: -46px;
        &-subtitle{
            margin-top: 12px;
            font-family: "Montserrat";
            font-style: normal;
            font-weight: 500;
            font-size: 14px;
            line-height: 21px;
            width: 563px;
            letter-spacing: -0.2px;
            text-align: center;
            color: #FFFFFF;
            margin-left: -191px;
        }
    }
    .contact{
        background: #FBFBFB;
        &__wrapper{
            padding-top: 105px;
            margin-top: 93px;
            padding-bottom: 71px;
        }
        .tell__number{
            a{
                letter-spacing: 0.03em;
                margin-left: 0px;
            }
        }
    }
    .footer{
        margin-top: 43px;
    }
    .gallery__btn{
        margin-top: 67px;
        margin-left: -2px;
        letter-spacing: 0.1px;
    }
    .contact__adress {
        position: relative;
        padding-left: 34px;
        margin-bottom: 38px;
    }
    .contact__productivity {
        position: relative;
        padding-left: 34px;
        margin-bottom: 35px;
    }
    .contact__productivity-subtitle {
        font-family: "Montserrat";
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 21px;
        letter-spacing: 0.01em;
        color: #1A2F3F;
        width: 229px;
    }
}