.contact {
  
 .tell{
    margin-left: 0;
    flex-direction: row-reverse;
    margin-bottom: 49px;
    position: relative;
    padding-left: 30px;
    width: 280px;
    margin-bottom: 44px;
    margin-left: 3px;
 }
 .tell::before {
    content: "";
    background-image: url('../../img/svg/tel.svg');
    display: inline-block;
    width: 15px;
    position: absolute;
    height: 15px;
    left: 0px;
    top: 9px;
}
.tell__number{
    margin-left: 1px;
}
.tell__number a {
    font-family: 'Montserrat';
font-style: normal;
font-weight: 500;
font-size: 14px;
line-height: 21px;
letter-spacing: 0.03em;
color: #1A2F3F;
margin-left: 0px;
span{
    font-family: 'Montserrat';
font-style: normal;
font-weight: 500;
font-size: 14px;
line-height: 21px;
/* or 150% */

letter-spacing: 0.01em;
margin-right: -2px;
color: #1A2F3F;
}
}
.tell__numbers{
    margin-top: -3px;
}
.tell__imgs{
    margin-top: 3px;
    margin-right: 2px;
}
}
.container {
}
.contact__wrapper {
    padding: 98px 0 60px 4px;
    padding-top: 203px;
    @include flex-all-sb;
    padding-bottom: 58px;

}
.contact__title {
    font-family: 'Raleway';
font-style: normal;
font-weight: 800;
font-size: 45px;
line-height: 53px;
letter-spacing: 0.01em;
color: #1A2F3F;
margin-bottom: 83px;

margin-top: -2px;
}
.contact__email {
    margin-bottom: 45px;
    position: relative;
    padding-left: 32px;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 21px;
    letter-spacing: -0.1px;
    color: #1A2F3F;
    margin-left: 3px;
}
.contact__email::before {
    content: "";
    background-image: url('../../img/svg/email.svg');
    display: inline-block;
    width: 15px;
    position: absolute;
    height: 15px;
    left: 0px;
    top: 4px;
}
.contact__adress {
    position: relative;
    padding-left: 34px;
    margin-bottom: 37px;
}
.contact__adress::before {
    content: "";
    background-image: url('../../img/svg/home.svg');
    display: inline-block;
    width: 16px;
    position: absolute;
    height: 16px;
    top: 0px;
    left: 2px;
}
.contact__adress-title {
    font-family: 'Raleway';
font-style: normal;
font-weight: 800;
font-size: 17px;
line-height: 20px;
letter-spacing: 0.01em;
color: #1A2F3F;
margin-bottom: 17px;
}
.contact__adress-subtitle {
    font-family: 'Montserrat';
font-style: normal;
font-weight: 500;
font-size: 14px;
line-height: 21px;
letter-spacing: 0.01em;
color: #1A2F3F;
width: 151px;
span{
        letter-spacing: -0.4px;
    }
}
.contact__productivity {
    position: relative;
    padding-left: 34px;
    margin-bottom: 41px;
}
.contact__productivity::before {
    content: "";
    background-image: url('../../img/svg/tree.svg');
    display: inline-block;
    width: 19px;
    position: absolute;
    height: 16px;
    left: 3px;
}
.contact__productivity-title {
    font-family: 'Raleway';
    font-style: normal;
    font-weight: 800;
    font-size: 17px;
    line-height: 20px;
    letter-spacing: 0.01em;
    color: #1A2F3F;
    margin-bottom: 17px;
}
.contact__productivity-subtitle {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 21px;
    letter-spacing: 0.01em;
    color: #1A2F3F;
    width: 229px;
}
.contact__right {
}
.contact__left{
    margin-top: -6px;
}