.article__block{
    margin-left: 17px;
    &-wrapper{
        display: flex;
        justify-content: space-between;
    }
    &-left{
        margin-left: 50px;
        padding-top: 71px;
    }
    &-img{
        max-width: 850px;
    }
    .data{
        font-family: 'Montserrat';
font-style: normal;
font-weight: 500;
font-size: 13px;
line-height: 22px;
letter-spacing: 0.01em;
color: #8A8A8A;
margin-left: -1px;

    }
    &-title{
        font-family: 'Raleway';
font-style: normal;
font-weight: 700;
font-size: 22px;
line-height: 26px;
letter-spacing: -0.01em;
margin-top: 20px;
color: #1A2F3F;
width: 408px;

    }
    &-subtitle{
        font-family: 'Raleway';
font-style: normal;
font-weight: 500;
font-size: 16px;
line-height: 25px;
color: #1A2F3F;
margin-top: 21px;
letter-spacing: -0.013em;
width: 409px;
margin-left: -1px;
span{
    letter-spacing: -0.01px;
    margin-left: 3px;
}
    }
   
    
}
.article__btn{
    font-family: 'Raleway';
font-style: normal;
font-weight: 700;
font-size: 14px;
line-height: 20px;
letter-spacing: 0.01em;
color: #1A2F3F;
background: none;
border: none;
padding-right: 4px;
}
.article__btn-group{
        @include flex-ai-center;
        margin-left: -8px;
        margin-top: 26px;
        
}
.article__block-subtitle2{
    margin-top: -3px;
    letter-spacing: -0.008em;
    width: 417px;
    line-height: 25px;
    margin-left: -1px;
    span{
        margin-left: 2px;
        letter-spacing: 0;
    }
}