.news{
    margin-left: 14px;
    &__inner{
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    &__title{
        @include titleStyle;
        margin-top: 5px;
    }
    &__subtitle{
        font-family: 'Raleway';
font-style: normal;
font-weight: 500;
font-size: 18px;
line-height: 22px;
text-align: center;
letter-spacing: 0em;
color: #1A2F3F;
margin-top: 31px;
margin-left: 1px;
    }
    &__items{
        margin-top: 71px;
        margin-left: -6px;
        @include flex;
    }
    .arrows{
        margin-right: 14px;
        margin-top: 15px;
    }
   
}
.news__item{
    width: 408px;
    position: relative;
    .play{
        position: absolute;
        left: 43.4%;
        top: 21%;
    }
    &-img{
        max-width: 408px;
        margin-bottom: 18px;
       video{
        object-fit: cover;
       }
    }
    .data{
        font-family: 'Montserrat';
font-style: normal;
font-weight: 500;
font-size: 13px;
line-height: 22px;
letter-spacing: 0.01em;
color: #8A8A8A;
padding-left: 23px;
margin-bottom: 9px;
    }
    &-title{
        font-family: "Raleway";
        font-style: normal;
        font-weight: 700;
        font-size: 16px;
        line-height: 23px;
        letter-spacing: -0.007em;
        padding-left: 25px;
        color: #1A2F3F;
        width: 349px;
        margin-bottom: -3px;
    }
    &-title2{
        font-family: "Raleway";
        font-style: normal;
        font-weight: 700;
        font-size: 16px;
        line-height: 22px;
        letter-spacing: -0.007em;
        padding-left: 25px;
        color: #1A2F3F;
        width: 363px;
        margin-bottom: -3px;
        span{
            margin-left: 2px;
        }
    }
    &-title3{
        line-height: 21px;
        margin-bottom: 15px;
        letter-spacing: -0.04em;
        padding-left: 22px;
        color: #1A2F3F;
        width: 344px;
    }
    &-subtitle{
        font-family: "Raleway";
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 20px;
    letter-spacing: -0.01px;
    color: #1A2F3F;
    padding-left: 24px;
    width: 349px;
    margin-bottom: 21px;
    }
    &-subtitle2{
        padding-left: 23px;
        line-height: 20px;
        letter-spacing: 0.01px;
        margin-top: 16px;
        margin-bottom: 21px;
    }
    &-subtitle3{
        padding-left: 22.6px;
        line-height: 20px;
        letter-spacing: 0.01px;
        margin-bottom: 22px;
        margin-top: 1px;
    }
   
    &-group{
        position: relative;
        padding-left: 18px;
        @include flex-ai-center;
        img{
            position: absolute;
            left: 73px;
            top: -1px;
        }
        img:hover{
            fill:#337D5A; ;
        }
    }
    &-btn{
        background: none;
        border: none;
    }
    &-btn:hover{
        color: #337D5A;
    }
  
}
.mySwiper{
    width: 1282px;
    margin:0;
    left: 7px;
    bottom: 7px;
}
.swiper__slide{
    width: 408px;
}
.swiper-button-prev{
    display: block;
    top:4px;
   left: -6px;
}
.swiper-button-next{
    display: block;
   right: 1px;
   top: 4px;

   
}
.news__btn{
    width: 100%;
    display:flex;
    justify-content: center;
    margin-top: 92px; 
}
.news__button{
    font-family: "Raleway";
    font-style: normal;
    font-weight: 700;
    font-size: 15px;
    line-height: 18px;
    letter-spacing: 0.02em;
    color: #337D5A;
    border: 1.5px solid #337D5A;
    background: white;
    padding: 16px 30px 13px 21px;
    padding-left: 25px;
    padding-right: 21px;
    margin-left: -15px;


}