

.password__change-page{
    .contact .tell {
        margin-left: 0;
        -webkit-box-orient: horizontal;
        -webkit-box-direction: reverse;
        -ms-flex-direction: row-reverse;
        flex-direction: row-reverse;
        margin-bottom: 49px;
        position: relative;
        padding-left: 30px;
        width: 280px;
        margin-bottom: 45px;
        margin-left: 3px;
    }
    .contact__wrapper {
        padding-bottom: 111px;
        padding-top: 201px;
    }
}

.password__wrapper {
    padding-top:107px;
    padding-bottom: 8px;
    display: flex;
    flex-direction: column;
   align-items: center;
}
.password__form{
    display: flex;
    flex-direction: column;
}
.password__input{
    width: 395px;
    height: 47px;
    border: 0.5px solid rgba(0, 0, 0, 0.15);
    margin-bottom: 11px;
    outline: none;
    padding-left: 11px;

}
.password__input::placeholder{
    font-family: 'Montserrat';
font-style: normal;
font-weight: 500;
font-size: 14px;
line-height: 17px;
color: #1A2F3F;
}
.password__input:nth-child(3)::placeholder{
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    color: #1A2F3F;
    letter-spacing: -0.25px;
}
.password__input:nth-child(1)::placeholder{
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    color: #1A2F3F;
    letter-spacing: -0.2px;
}
.password__title {
    margin-bottom:43px;
    margin-left: 6px;
    h1{
font-family: 'Raleway';
font-style: normal;
font-weight: 800;
font-size: 32px;
line-height: 32px;
letter-spacing: 0.01em;
color: #1A2F3F;
    }
}
.password__btn {
    width: 410px;
    height: 53px;
    background: #337D5A;
    border: 0.5px solid #337D5A;
    text-align: center;
    font-family: "Raleway";
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 17px;
    margin-left: 2px;
    color: #FFFFFF;
}