.record__page{
    .contact__wrapper{
        padding-top: 211px;
        padding-bottom: 112px;
    }
}
.record__page-wrapper{
    @include flex;
   
}
.record__img{
    width: 440px;
height: 525px;
background: url("../../img/record.png");
background-position: center;
background-size: cover;
background-repeat: no-repeat;
margin-left: 23px;
padding-top: 326px;
margin-top: 234px;
