.footer{
    position: relative;
    padding-bottom: 41px;
    .list__item:nth-child(2){
        a{
            margin-right: 3px;
        margin-left: 7px;
        letter-spacing: 0;
        }
        
    }
}

.footer-main{
    @include flex-all-sb;
    padding-left: 6px;
    .list{
        padding-left: 0px;
        padding-right: 0px;
        width: 1004px;
        padding-top: 14px;
        

    };
    nav{
        margin-top: 3px;
    }
    .list__item:nth-child(3){
        margin-left: -4px;
    }
    .list__item:nth-child(5){
margin-right: -4px;
    }
    .list__item:last-child{
        margin-right: 7px;
        a{
            margin-left: 2px;
            margin-right: -1px;
        }
    }
    
}
.line{
    
        position: absolute;
        width: 100%;
        height: 1px;
        background: rgba(0, 0, 0, 0.08);;
        left: 0px;
        top: 85px;
    
}


.footer__copyright{
    @include flex-all-sb;
    padding-top: 43px;
    padding-left: 7px;
    width: 1309px;

    &-dev{
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 400;
        font-size: 12px;
        line-height: 15px;
        
        color: #1A2F3F;
        span{
            color:#337D5A;
            font-weight: 600;
            span{
                font-weight: 400;
            }
        }
    }
    &-text{
        font-family: "Montserrat";
        font-style: normal;
        font-weight: 400;
        font-size: 12px;
        line-height: 15px;
        color: #1A2F3F;
        letter-spacing: -0.1px;
        margin-right: 87px;
    }
  
}