.list{
    @include flex;
    padding-left: 86px;
    padding-right: 84px;
    padding-top: 15px;
    justify-content: space-between;
    width: 1139px;
   
    &__item{
        
        a{
            font-family: 'Raleway';
font-style: normal;
font-weight: 500;
font-size: 15px;
line-height: 18px;
letter-spacing: 0.02em;
color: #1A2F3F;
        }
    }
    &__item.active{
        a{
              color: #337D5A;
        border-bottom: 3px solid #337D5A;
        padding-bottom: 11px;
        }
    }
  
    
}
.list__item:nth-child(4){
    a{
        letter-spacing: 0.1px;
        margin-left: 4px;
        margin-right: 4px;
    }
}