.page__body-gallery{
    .contact__wrapper{
        padding-top: 210px;
        padding-bottom: 114px;
    }
    .contact__title {
        font-family: "Raleway";
        font-style: normal;
        font-weight: 800;
        font-size: 45px;
        line-height: 53px;
        letter-spacing: 0.01em;
        color: #1A2F3F;
        margin-bottom: 88px;
        margin-top: 0px;
    }
    .title__video{
        background: url('../../img/gallery-title-bg.png');
        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;
        margin-bottom: 90px;
        img{
            display: none;
        }
        video{
            display: none;
            
            
        }
        &-text{
            position: absolute;
             top: 197px;
            left: 990px;
        }
        &-title{
            font-family: "Raleway";
            font-style: normal;
            font-weight: 800;
            font-size: 62px;
            line-height: 73px;
            letter-spacing: 0.01px;
            color: #FFFFFF;
            margin-bottom: 13px;
            margin-top: -41px;
            margin-left: -5px;
        }
        
        &-subtitle {
            font-family: "Raleway";
            font-style: normal;
            font-weight: 600;
            font-size: 25px;
            line-height: 29px;
            text-align: center;
            letter-spacing: 0px;
            color: #FFFFFF;
            width: 540px;
            margin-bottom: 15px;
            margin-left: -4px;
        }
    }

    .gallery__block{
        margin-bottom: -2px;
    }
}
.gallery__button{
     @include flex-all-center
}
.gallery__btn{
    @include button1;
    width: 201px;
    height: 52px;
    margin-top: 64px;
    margin-top: 66px;
    margin-left: 1px;
    letter-spacing: -0.4px;
   
}