.eco{
    background: url("../../img/eco-bg.png");
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    height: 740px;
    margin-bottom: 80px;
    &__wrapper{
        height: 100%;
        width: 100%;
        @include flex-all-center;
        flex-direction:column;
        padding-top: 224px;
    }
    &__title{
        @include titleEco;
        margin-top: 40px;
        letter-spacing: 0;
        margin-left: -20px;
    }
    &__subtitle{
        @include subtitleEco;
        width: 660px;
        margin-top: 20px;
        letter-spacing: -0.09px;
        margin-left: 10px;
        line-height: 30px;
    }
}