.error{
    &__block{
        @include flex-all-center;
        flex-direction: column;
        padding-top: 111px;
    }
    &__img{
        padding-left: 467px;
    }
    &__title{
        font-family: 'Raleway';
font-style: normal;
font-weight: 500;
font-size: 18px;
line-height: 30px;
text-align: center;
letter-spacing: 0.01em;
color: #1A2F3F;
padding-top: 29px;
padding-left: 2px;
    }
    &__button{
        @include button1;
        width: 332px;
        height: 50px;
        position: relative;
        margin-top: 17px;
        padding-left: 33px;
        padding-top: 4px;
    }
    &__button::before{
       
        content: "";
        display: inline-block;
        background: url("../../img/svg/right__arrow.svg");
        height: 14px;
        width: 14px;
        position: absolute;
        left: 25px;
        top: 19px;
    }
    .contact__wrapper{
        padding-top: 210px;
        padding-bottom: 113px;
    }
}