
.table__responsive{
   
        width: 992px;
        margin-left: 48px;
        margin-top: 63px;
    
 
}
.table{
    width: 100%;
    border-collapse: collapse;
    img{
        margin-right: 17px;
       padding-bottom: 1px;
       
    }

}
.watch{
    top: -29px;
    left: 905px;
    background: url("../../img/watch__bg.png");
    width: 106px;
    position: absolute;
    opacity: 0;
    transition: all ease 1s;
height: 50px;
    &__title{
        font-family: 'Raleway';
font-style: normal;
font-weight: 700;
font-size: 13px;
line-height: 14px;
text-align: center;
color: #FFFFFF;
padding-top: 8px;
    }
}
.header__history-transition{
    .history__order-title:nth-child(1){
        padding-left: 49px;
        padding-right: 22px;
    }
    .history__order-title:nth-child(1)::after{
        right: 6px;
       
    }
    .history__order-title:nth-child(2) {
        width: 119px;
        padding-left: 292px;
        padding-right: 36px;
    }
    .history__order-title:nth-child(2)::after{
        right: 18px;
        
     }
     .history__order-title:nth-child(3) {
        width: 209px;
        padding-left: 323px;
    padding-right: 40px;
    }
    .history__order-title:nth-child(3)::after{
        right: 31px;
        
     }
}
.historyTransition__table-content{
    .data{
            padding-left: 33px;
    }
    .status {
        padding-left: 260px;
        padding-bottom: 2px;
        letter-spacing: 0em;
    }
    .history__price{
        padding-left: 277px;
        padding-right: 0;
        padding-bottom: 1px;
    }
    .view {
        cursor: pointer;
        fill: #8A8A8A;
        margin-left: -37px;
        margin-right: 22px;
        padding-top: 10px;
    }
    .view:hover{
        fill: #337D5A;
        
    }
    .view:hover~.watch{
       opacity: 1;
        
    }
    
    .content-row{
        position: relative;
    }
    
}
  