.main__video {
    position: relative;
    height: 740px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: url('../../img/title-poster-main.png');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}
.container {
}
.content {
    position: absolute;
   display: flex;
   justify-content: center;
   align-items: center;
   margin-top: 49px;
   flex-direction: column;
    z-index: 2;
    span{
        font-family: "Raleway";
        font-style: italic;
        font-weight: 500;
        font-size: 24px;
        line-height: 28px;
        letter-spacing: -0.018em;
        margin-top: 29px;
        color: #FFFFFF;
        margin-left: -16px;
    }
}
.main__video-title {
   
  
        font-family: "Raleway";
        font-style: normal;
        font-weight: 800;
        font-size: 45px;
        line-height: 53px;
        text-align: center;
        letter-spacing: 0em;
        color: #FFFFFF;
        margin-bottom: 70px;
        margin-top: -12px;
    width: 1185px;
    margin-left: -13px;
    
    
    span{
        font-family: 'Raleway';
        font-style: normal;
        font-weight: 800;
        font-size: 45px;
        line-height: 53px;
        text-align: center;
        letter-spacing: 0em;
        color: #FFFFFF;
        margin-left: 2px;
    }
    
}

.info__item:nth-child(2) .info__subtitle {
    margin-left: 6px;
    letter-spacing: 0px;
    margin-top: 4px;
}

.main__video-content {
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;
    left: 0;

}
.main__play-btn{
    margin-left: -13px;
}