.header{
    padding-top: 9px;
}
.header__top{
    @include  flex-all-sb;

}
.header__top-title{
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 17px;
    color: #337D5A;
    letter-spacing: 0px;
    margin-left: 165px;

span{
    margin-left: 2px;
    font-family: 'Montserrat';
font-style: normal;
font-weight: 600;
font-size: 14px;
line-height: 17px;

color: #337D5A;

}
}
.header__top-end{
    @include flex-ai-center;
    margin-right: 9px;
}
.header__sign{
    @include flex-ai-center;
    &-text{
    font-family: 'Raleway';
    font-style: normal;
    font-weight: 500;
    font-size: 13px;
    line-height: 15px;
    letter-spacing: 0.02em;
    color: #323232;
    margin-left: 6px;
    margin-right: -1px;
    }
    &-text.active{
        border: 2px dashed #93B474;
    }
}
.header__log{
    margin-left: 27px;
    margin-right: 51px;
    &-text{
    font-family: 'Raleway';
    font-style: normal;
    font-weight: 500;
    font-size: 13px;
    line-height: 15px;
    letter-spacing: 0.02em;
    color: #323232;
    
    }
    &-text.active{
        border: 2px dashed #93B474;
    }
}
.language{
    @include flex-ai-center;
    &__title{
        font-family: 'Raleway';
font-style: normal;
font-weight: 500;
font-size: 13px;
line-height: 15px;
letter-spacing: 0.02em;
color: #000000;

    }
}
// Header center
.header__center{
    @include flex-all-sb;
    padding-top: 26px;
    &-end{
        @include flex-ai-center;
        margin-top: -9px;
        margin-right: 42px;
        
    }
    &-btn{
        background: #337D5A;
        padding: 13px 22px;
        width: 149px;
        font-family: 'Raleway';
font-style: normal;
font-size: 13px;
line-height: 15px;
letter-spacing: 0.02em;
color: #FFFFFF;
border: none;
margin-top: -1px;

    }
    &-subtitle{
        font-family: 'Montserrat';
font-style: normal;
font-weight: 500;
font-size: 12px;
line-height: 15px;
letter-spacing: 0.02em;
color: #8A8A8A;
width: 178px;
margin-left: 13px;
    }
    .logo{
        margin-left: 8px;
    }
   
}
.cart{
    position: relative;
    margin-left: 29px;
margin-right: -29px;
    &__btn{
        width: 16px;
        text-align: center;
        border: none;
        height: 16px;
        background: #93B474;
        border-radius: 50%;
        font-family: 'Montserrat';
font-style: normal;
font-weight: 700;
font-size: 8px;
line-height: 10px;
color: #FFFFFF;
position: absolute;
left: 12px;


    }
}
// header__bottom
