

.registration__block-after::after{
    content: "Безплатно";
    display: inline-block;
    position: absolute;
    background: url(../../img/sticker.png);
    width: 117px;
    height: 38px;
    background-repeat: no-repeat;
    left: 169px;
    font-family: "Raleway";
    font-style: normal;
    font-weight: 800;
    font-size: 12px;
    line-height: 12px;
    letter-spacing: 0.1px;
    color: #FFFFFF;
    padding-left: 13px;
    padding-top: 5px;
    top: -4px;

}
.registration__block-input2:last-child{
    margin-top: -2px;
}
.input__text{
    font-family: 'Montserrat';
font-style: normal;
font-weight: 500;
font-size: 14px;
line-height: 17px;
color: #1A2F3F;
margin-left: 5px;
}

.registration__block-togle{
    height: 0;
    padding: 0;
    transform: translateX(-810px);
    transition: all ease 1s;
}
.registration__block-togle.active{
    height: 124px;
    transform: translateX(0px);
}
.registration__block-adress.active{
    height: 62px;
    padding-bottom: 2px;
    transform: translateX(0px);
}
.registration__block-ml:last-child .input__text{
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    color: #1A2F3F;
    letter-spacing: 0px;
    margin-left: 6px;
}
