.product__wrapper{
    padding: 87px 10px 100px 0
}
.container {
}
.products__text {
    margin-left: 11px;
    margin-top: -3px;
}
.product__title{
    margin-bottom: 11px;
    h3{
          @include titleEco;
          color:#1A2F3F;
          text-align: center;
          letter-spacing: 0;
    }
  
}
.product__subtitle {
    display: flex;
    justify-content: center;
    margin-left: 5px;
    p{
        font-family: "Raleway";
        font-style: normal;
        font-weight: 500;
        font-size: 18px;
        line-height: 27.5px;
        text-align: center;
        letter-spacing: 0px;
        color: #1A2F3F;
        width: 570px;
        margin-left: -2px;

    }
}

.product__item-image {
    margin-bottom: 15px;
    margin-left: 8px;
}
.products__item {
    width: 410px;
    height: 525px;
    display: flex;
    position: relative;
    flex-direction: column;
  transition: all ease 1s;
  cursor: pointer;


}
.products__item:last-child .product__item-image{
    margin-top: -3px;
    margin-left: -17px;
}
.products__item:first-child .products__content{
    padding-top: 178px;
}
.products__item:last-child .product__item-title{
    margin-left: 0px;
}
.product__item-title {
    margin-bottom: 26px;
font-family: 'Raleway';
font-style: normal;
font-weight: 800;
font-size: 40px;
line-height: 47px;
letter-spacing: 0.01em;
margin-left: 2px;
color: #FFFFFF;

}
.products__items{
    display:flex;
    justify-content: space-between;
    margin-top: 46px;
    margin-left: 8px;
   
}

.product__item-subtitle {
    font-family: 'Raleway';
font-style: normal;
font-weight: 600;
font-size: 18px;
line-height: 23px;
text-align: center;
letter-spacing: 0.01em;
color: #FFFFFF;
width: 330px;
margin-top: 16px;
display: none;
}
.product__item-subtitle1{
    font-family: 'Raleway';
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    line-height: 23px;
    text-align: center;
    letter-spacing: 0.01em;
    color: #FFFFFF;
    width: 334px;
    display: none;
}
.products__item-bg{
    opacity: 1;

}
.products__content{
    position: absolute;
    display: flex;
    z-index: 2;
    flex-direction: column;
    align-items: center;
    width: 100%;
    padding-top: 203px;
}
.products__item:hover{
    background: #337D5A;
}
.products__item:hover .products__content{
    padding-top: 94.6px;
}
.products__item:hover{
    .product__item-subtitle{
   display: block;
   font-family: "Raleway";
   font-style: normal;
   font-weight: 600;
   font-size: 18px;
   line-height: 24.7px;
   text-align: center;
   letter-spacing: -0.0em;
   color: #FFFFFF;
   width: 330px;
   margin-top: 17px;
   margin-left: -5px;
   span{
    margin-left: 3px;
   }
}
.product__item-image {
    margin-bottom: 5px;
    margin-left: 9px;
}
.product__item-title {
    margin-bottom: 21px;
    font-family: "Raleway";
    font-style: normal;
    font-weight: 800;
    font-size: 40px;
    line-height: 47px;
    letter-spacing: -0.0em;
    margin-left: 3px;
    color: #FFFFFF;
}
} 
.products__item:hover .product__item-subtitle1{
    display: block;
    font-family: "Raleway";
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    line-height: 23px;
    text-align: center;
    letter-spacing: -0.008em;
    color: #FFFFFF;
    width: 324px;
    margin-left: -6px;
    span{
        margin-left: 7px;
    }
 }
 .products__item:hover .products__item-bg{
    opacity: 0.05;

}
