.gallery__block1{
    &-wrapper{
        @include flex-ai-center;
        margin-top: 31px;
        margin-left: 6px;
    }
    &-item{
        margin-right: 31px;
        position: relative;
        transition: all ease 0.5s;
        
    }
    &-item1{
        position: relative;
        transition: all ease 0.5s;
    }
    
}
.gallery__block .gallery__block1{
    
    &-wrapper{
        margin-top: 28px;
        flex-direction: row-reverse;
    }
    &-item{
        margin-right: 9px;
        margin-left: 33px;
    }
    
}
.gallery__block1-text{
    position: absolute;
    top: 79px;
    left: 40px;
        .gallery__block1-title{
            font-family: 'Montserrat';
            opacity: 0;
            font-style: normal;
            font-weight: 800;
            font-size: 22px;
            line-height: 27px;
            letter-spacing: -0.01em;

            color: #FFFFFF;
        }
        .gallery__block1-subtitle{
            font-family: 'Montserrat';
font-style: normal;
font-weight: 500;
font-size: 15px;
opacity: 0;
line-height: 22px;
width: 407px;
letter-spacing: -0.35px;
color: #FFFFFF;
margin-top: 23px;
margin-left: 0px;
span{
    letter-spacing: -0.1px;
}

        }
        .gallery__block1-subtitle1{
