.title__video {
    position: relative;
    height: 600px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: url('../../img/title-poster-main.png');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    margin-bottom: 81px;
    .video__play-btn{
        margin-top: -5px;
    }
}
.container {
}
.content {
    position: absolute;
   display: flex;
   justify-content: center;
   align-items: center;
   margin-top: 49px;
   flex-direction: column;
    z-index: 2;
}
.title__video-title {
   
    font-family: "Raleway";
    font-style: normal;
    font-weight: 800;
    font-size: 62px;
    line-height: 73px;
    letter-spacing: 0.01em;
    color: #FFFFFF;
    margin-bottom: 33px;
    margin-top: 6px;
    
    
}
.title__video-subtitle {
    font-family: "Raleway";
    font-style: normal;
    font-weight: 600;
    font-size: 25px;
    line-height: 31px;
    text-align: center;
    letter-spacing: 0.06px;
    color: #FFFFFF;
    width: 540px;
    margin-bottom: 44px;
    margin-left: -6px;
}
.title__video-content {
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;
    left: 0;

}
