.payment__tabs {
}
.payment__tabs-wrapper {
}
.payment__nav {
    border-bottom: 1px solid rgba(0, 0, 0, 0.08);
}
.payment__list {
    padding: 0px 125px;
    @include flex-all-sb;
    margin-top: 3px;
    margin-bottom: -3px;
}
.payment__item {
    @include flex-all-sb;
    width: 196px;
    padding-bottom: 10px;
   
    svg{
        fill: #c4c4c4;
    }
    transition: all ease 0.3s;
}
.payment__nav-title{
    font-family: 'Raleway';
font-style: normal;
font-weight: 800;
margin-top: -2px;
font-size: 20px;
line-height: 23px;
letter-spacing: 0.01em;
color: #1A2F3F;
}
.payment__item:nth-child(1){
    margin-bottom: 5px;
    .payment__nav-title{
        margin-top: 4px;
    }
}
.payment__item:nth-child(2){
    width: 239px;
    margin-left: 22px;
    margin-top: 1px;
}
.payment__item:nth-child(3){
    width: 210px;
    margin-right: 2px;
}
.payment__item.active{
    border-bottom: 3px solid 
    #337D5A;
    svg{
        fill: #337D5A;
    }
}
.payment__block{
    display: none;
}
.payment__block.active{
    @include  flex-all-sb;
}

.payment__blocks {
    margin-top: 59px;
}
.payment__block {
    &-title{
        font-family: 'Raleway';
font-style: normal;
font-weight: 800;
font-size: 18px;
line-height: 24px;
margin-left: 8px;
/* or 133% */


color: #1A2F3F;
    }
}

.payment__block-items {

        margin-left: 8px;
        margin-top: 24px;
}
.payment__block-item {
    margin-bottom: 39px;
    
}
.payment__block-item:nth-child(2){
    margin-bottom: 36px;
    .payment__item-title{
        font-family: "Raleway";
    font-style: italic;
    font-weight: 600;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: 0.0px;
    margin-left: 15px;
    color: #1A2F3F;
    }
    .payment__item-subtitle {
        font-family: "Raleway";
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 24px;
        margin-left: 2px;
        letter-spacing: -0.2px;
        margin-top: 9px;
        width: 436px;
        color: #1A2F3F;
        span{
            letter-spacing: -0.1px;
        }
    }
}
.payment__block-item:nth-child(3){
    .payment__item-title {
        font-family: "Raleway";
        font-style: italic;
        font-weight: 600;
        font-size: 16px;
        line-height: 24px;
        letter-spacing: 0.2px;
        margin-left: 15px;
        color: #1A2F3F;
    }
    .payment__item-subtitle {
        font-family: "Raleway";
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 24px;
        margin-left: 2px;
        letter-spacing: -0.19px;
        margin-top: 10px;
        width: 440px;
        color: #1A2F3F;
    }
}
.payment__item-title {
    font-family: "Raleway";
    font-style: italic;
    font-weight: 600;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: 0.07px;
    margin-left: 15px;
