.repair-page{
  .contact__wrapper{
    padding-top: 172px;
    padding-bottom: 111px;
  }
  .registration__block-input2{
    margin-bottom: 11px;
    position: relative;
    margin-top: 1px;
    input{
        padding-left: 0px;
        height: 12px;
        appearance: none;
        -webkit-appearance: none;
        border-radius: 50%;
        background: #ffffff;
        border: 2px solid #FFF;
      
        /* The outline will be the outer circle */
        outline: 2px solid #337D5A;
    }
    input:checked{
        padding-left: 0px;
        height: 12px;
        appearance: none;
        -webkit-appearance: none;
        border-radius: 50%;
        background: #337D5A;
        border: 2px solid #FFF;
      
        /* The outline will be the outer circle */
        outline: 2px solid #337D5A;
    }
    .input__text1{
        margin-left: 6px;
    }
}
}

.repair__block {
  margin-bottom: -4px;
 &-title{
  margin-left: 30px;
  margin-bottom: 5px;

  h2{
      font-family: 'Raleway';
font-style: normal;
font-weight: 600;
font-size: 18px;
line-height: 32px;
letter-spacing: 0.01em;
color: #1A2F3F;
position: relative;
  }


 }
  &-subtitle{
    font-family: 'Raleway';
font-style: normal;
font-weight: 500;
font-size: 15px;
line-height: 20px;
text-align: center;
letter-spacing: 0.01em;
color: #8A8A8A;
width: 398px;
span{
  font-family: 'Raleway';
font-style: normal;
font-weight: 600;
font-size: 15px;
line-height: 20px;
letter-spacing: 0.01em;
color: #1A2F3F;

}
 }
}

.repair__block-title h2::before{
  content: '';
  display: inline-block;
  position: absolute;
  background: url('../../img/svg/locked.svg');
  width: 19px;
  height: 19px;
  left: -25px;
  top: 5px;
}
.repair__title{

  font-family: 'Raleway';
  font-style: normal;
  font-weight: 800;
  font-size: 32px;
  line-height: 32px;
  letter-spacing: 0.01em;
  color: #1A2F3F;
  margin-bottom: 21px;
}
.repair__block{
  display: flex;
  flex-direction: column;
  align-items: center;
}
.repair__form{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 43px;
}
.repair__wrapper{
  padding-top: 110px;
  display: flex;
  flex-direction: column;
  align-items: center;

}
.repair__btn {
  width: 245px;
  height: 53px;
  background: #337D5A;
  border: 0.5px solid #337D5A;
  text-align: center;
  font-family: "Raleway";
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 17px;
  margin-left: 4px;
  color: #FFFFFF;
}

.repair__input{
  width: 395px;
  height: 47px;
  border: 0.5px solid rgba(0, 0, 0, 0.15);
  margin-bottom: 11px;
  outline: none;
  margin-top: 19px;
  padding-left: 11px;

}
.repair__input::placeholder{
  font-family: 'Montserrat';
font-style: normal;
font-weight: 500;
font-size: 14px;
line-height: 17px;
color: #1A2F3F;


}
.repair__unlocked{
  margin-left: 32px;
  h2{
    letter-spacing: 0px;
  }
}
.repair__unlocked h2::before{
  content: '';
  display: inline-block;
  position: absolute;
  background: url('../../img/svg/unlocked.svg');
  width: 19px;
  height: 19px;
  left: -29px;
  top: 3px;
}
.repair__input-last{
  margin-top: 0;
}
.repair__subtitle-last{
  margin-left: -16px;
}
.repair__btn-last{
  margin-left: -3px;
  letter-spacing: -0.02em;
}